export default{
	data(){
		return{
			type:2,
			src:""
		}
	},
	methods:{
		service(){
			uni.navigateTo({
				url:"/pages/enter/service"
			})
		},
		save() {
			uni.showLoading({
				title: '图片保存中...',
			})
			let that=this;
			let urlP="";
			let x=0,y=0;
			if(this.type==0){
				urlP="../../static/invite/invite_1.png";
				x=80;
				y=140;
			}else{
				urlP="../../static/invite/invite_2.png"
				x=80;
				y=210;
			}
			let url=that.src;
			this.type = parseInt(Math.random() * 2, 10);
			const context = uni.createCanvasContext('myCanvas')
			context.drawImage(urlP, 0, 0, this.windowWidth, this.windowHeight)
			context.drawImage(that.src, x, y, 90, 90)
			context.draw(false, function() {
				uni.canvasToTempFilePath({
					canvasId: 'myCanvas',
					success: function(res) {
						that.tempFilePath=res.tempFilePath;
						uni.getImageInfo({
							src: that.tempFilePath,
							success: function(image) {
								console.log('图片信息：', JSON.stringify(image));
								that.type=that.type
								uni.saveImageToPhotosAlbum({
									filePath: image.path,
									success: function() {
										that.type=that.type
										uni.showToast({
											title: '图片保存成功',
											icon: 'none',
											duration: 2200
										});
									},
									fail: function() {
										uni.hideLoading()
										uni.showToast({
											title: "保存失败，请稍后重试",
											icon: "none"
										});
									}
								});
							}
						});
					}
				})
			})
		}
	},
	onLoad(e) {
		let that=this;
		uni.getStorage({
		 	key:"shopObj",
		 	success(res){
		 		let shopObj=JSON.parse(res.data);
		 		that.src="https://wx.hnlxyj.com/user/share/qrCode?block=1&width=800&height=800&url=http://www.hnlxyj.com/wx/register/enter.html?marchantId="+shopObj.merchantId
		 	}
		})
		this.type=e.type
	},
}
